<template>
  <div class="p2">
    <h1 class="uk-text-center uk-margin-large-top uk-margin-large-bottom">中华人民共和国企业所得税月(季)度预缴纳税申报表(A类,2017年版)</h1>
    <div style="width: 900px; margin: 0 auto">
      <div class="uk-text-center">税款所属期间: {{com.month}}-01日至{{com.month}}-30日</div>
      <div class="uk-text-left">纳税人识别号： {{com.firm_code}}</div>
      <div class="uk-text-left">纳税人名称: {{com.firm_name}}</div>
      <div class="uk-flex uk-flex-right">
        <p>注: <span style="width: 20px; height:10px; display:inline-block" class="bg-write"></span> 不能填写 </p>
        <span>　　金额单位：人民币元（列至角分）</span>
      </div>

    </div>
    <form>
      <table width="100%" cellpadding="0" cellspacing="0" border="1">
        <tr>
          <td colspan="2" style="text-align: center;">预缴方式</td>
          <td colspan="4" style="text-align: left;">
            <input type="radio" checked disabled
                   style="position: relative;top: 3px;left: 3px; width: 15px;height: 15px;">
            按照实际利润额预交
            <input type="radio" disabled style="position: relative;top: 3px;left: 3px; width: 15px;height: 15px;">
            按照上一纳税年度应纳税所得额平均额预缴
            <input type="radio" disabled style="position: relative;top: 3px;left: 3px; width: 15px;height: 15px;">
            按照税务机关确定的其他方法预交
          </td>
        </tr>
        <tr>
          <td colspan="2" style="text-align: center;">企业类型</td>
          <td colspan="4" style="text-align: left;">
            <input type="radio" checked disabled
                   style="position: relative;top: 3px;left: 3px; width: 15px;height: 15px;">
            一般企业
            <input type="radio" disabled style="position: relative;top: 3px;left: 3px; width: 15px;height: 15px;">
            跨地区经营汇总纳税企业总机构
            <input type="radio" disabled style="position: relative;top: 3px;left: 3px; width: 15px;height: 15px;">
            跨地区经营汇总纳税企业分支机构
          </td>
        </tr>
        <tr>
          <th colspan="6" style="text-align: center;">预　缴　税　款　计　算</th>
        </tr>
        <tr>
          <td style="text-align: center;">行次</td>
          <td colspan="4" style="text-align: center;">项目</td>
          <td style="text-align: center;">累计金额</td>
        </tr>
        <tr>
          <td style="text-align: center;">1</td>
          <td colspan="4">营业收入</td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="1_1"/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">2</td>
          <td colspan="4">营业成本</td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="2_1"/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">3</td>
          <td colspan="4">利润总额</td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="3_1"/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">4</td>
          <td colspan="4">加：特定业务计算的应纳税所得额</td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="4_1"/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">5</td>
          <td colspan="4">减：不征税收入</td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="5_1"/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">6</td>
          <td colspan="4">减：免税收入、减计收入、所得减免等优惠金额（填写A201010）</td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="6_1" v-model="form.a6_1" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">7</td>
          <td colspan="4">减：固定资产加速折旧（扣除）调减额（填写A201020）</td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="7_1" v-model="form.a7_1" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">8</td>
          <td colspan="4">减：弥补以前年度亏损</td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="8_1"/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">9</td>
          <td colspan="4">实际利润额（3+4-5-6-7-8）\ 按照上一纳税年度应纳税所得额平均额确定的应纳税所得额</td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="9_1" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">10</td>
          <td colspan="4">税率（25%）</td>
          <td>
            <input value="0.25" name="10_1" class="tiaochu" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">11</td>
          <td colspan="4">应纳税所得税额（9*10）</td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="11_1" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">12</td>
          <td colspan="4">减：减免所得税额（填写A201030）</td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="12_1" v-model="form.a12_1" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">13</td>
          <td colspan="4">减：实际已缴纳所得税额</td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="13_1" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">14</td>
          <td colspan="4">减：特定业务预缴（征）所得税额</td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="14_1"/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">15</td>
          <td colspan="4">本期应补（退）所得税额（11-12-13-14）\ 税务机关确定的本期应纳所得税额</td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="15_1" readonly/>
          </td>
        </tr>
        <tr>
          <th colspan="6" style="text-align: center;">汇总纳税企业总分机构税款计算</th>
        </tr>
        <tr>
          <td style="text-align: center;">16</td>
          <td rowspan="4" style="text-align: center;">总机构填报</td>
          <td colspan="3">总机构本期分摊应补（退）所得税额（17+18+19）</td>
          <td>
            <input value="0.00" name="16_1" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">17</td>
          <td colspan="3">其中：总机构分摊应补（退）所得税额（15×总机构分摊比例______%）</td>
          <td>
            <input value="0.00" name="17_1" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">18</td>
          <td colspan="3">　　　财政集中分配应补（退）所得税额（15×财政集中分配比例______%）</td>
          <td>
            <input value="0.00" name="18_1" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">19</td>
          <td colspan="3">　　　总机构具有主体生产经营智能的部门分摊所得税额（15×全部分支机构分摊比例______% × 总机构具有主体生产经营职能部门分摊比例______%）</td>
          <td>
            <input value="0.00" name="19_1" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">20</td>
          <td rowspan="2" style="text-align: center;">分支机构填报</td>
          <td colspan="3">分支机构本期分摊比例</td>
          <td>
            <input value="0.00" name="20_1" class="tiaochu" readonly/>
          </td>
        </tr>
        <tr>
          <td style="text-align: center;">21</td>
          <td colspan="3">分支机构本期分摊应补（退）所得税额</td>
          <td>
            <input value="0.00" name="21_1" readonly/>
          </td>
        </tr>
        <tr>
          <th colspan="6" style="text-align: center;">附　报　信　息</th>
        </tr>
        <tr>
          <td colspan="2">小型微利企业</td>
          <td style="text-align: center;">
            <input type="radio" checked name="radA"
                   style="position: relative; top: 3px;left: 3px; width: 15px;height: 15px;">
            是
            <input type="radio" name="radA" style="position: relative; top: 3px;left: 3px; width: 15px;height: 15px;">
            否
          </td>
          <td style="text-align: center;">科技型中小企业</td>
          <td colspan="2" style="text-align: center;">
            <input type="radio" name="radB" style="position: relative; top: 3px;left: 3px; width: 15px;height: 15px;">
            是
            <input type="radio" name="radB" checked
                   style="position: relative; top: 3px;left: 3px; width: 15px;height: 15px;">
            否
          </td>
        </tr>
        <tr>
          <td colspan="2">高新技术企业</td>
          <td style="text-align: center;">
            <input type="radio" name="radC" style="position: relative; top: 3px;left: 3px; width: 15px;height: 15px;">
            是
            <input type="radio" name="radC" checked
                   style="position: relative; top: 3px;left: 3px; width: 15px;height: 15px;">
            否
          </td>
          <td style="text-align: center;">技术入股递延纳税事项</td>
          <td colspan="2" style="text-align: center;">
            <input type="radio" name="radD" style="position: relative; top: 3px;left: 3px; width: 15px;height: 15px;">
            是
            <input type="radio" name="radD" style="position: relative; top: 3px;left: 3px; width: 15px;height: 15px;">
            否
          </td>
        </tr>
        <tr>
          <td colspan="5">期末从业人数</td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="22_1"/>
          </td>
        </tr>
        <tr>
          <td colspan="6">
            谨声明:此纳税申报表是根据《中华人民共和国企业所得税法》，《中华人民共和国企业所得税法实施条例》和国家有关税收规定填报的，是真实的，可靠的，完整的。<br/><br/>
            　　　　　　　　　　　　　　　　　　　　法定代表人（签字）：　　年　　月　　日
          </td>
        </tr>
        <tr>
          <td colspan="2" width="30%" style="height:200px; text-align:left;">
            纳税人公章：<br/><br/><br/><br/> 会计主管：
            <br/><br/><br/><br/> 填表日期　　　年　　　月　　　日
          </td>
          <td style="text-align:left;">
            代理申报中介机构公章：<br/><br/><br/><br/> 经办人：
            <br/><br/> 经办人执业证件号码:
            <br/><br/> 代理申报日期　　　年　　　月　　　日
          </td>
          <td colspan="3" width="30%">
            主管税务机关受理专用章：<br/><br/><br/><br/><br/><br/> 受理人：
            <br/><br/> 受理日期　　　年　　　月　　　日
          </td>
        </tr>
      </table>
    </form>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
      <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 4,  //当前表格的名称编号
        setting: null,
        form: {
          'a13_1': '0.00', 'a6_1': '0.00', 'a7_1': '0.00', 'a12_1': '0.00'
        },
        com: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
      this.formrevise()  //获取表间数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})

        //获取预设数据
        //获取通用企业信息
        this.com = this.$store.state.companyCurrent.gscompany
        //获取预设数据
        this.setting = this.$store.state.companyCurrent.content.page4
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        this.fn1(9, [3, 4, 5, 6, 7, 8], 2)
        this.fn1(11, [9, 10], 3)
        this.fn1(15, [11, 12, 13, 14], 4)
        this.fn1(16, [17, 18, 19], 1)
      },
      //单列运算
      // q  需计算单元格的行
      // gump 参与计算的行
      // type 公式{
      //  1:a+b+c...
      //  2:a+b-c-d-...
      //  3:a*b*c*...
      //  4:a-b-c-d-...
      // }
      fn1(q, gump, type) {
        if (J('input[name=' + gump[0] + '_1]').val()) {
          var sum = parseFloat(J(' input[name=' + gump[0] + '_1]').val())
        } else {
          var sum = 0
        }
        for (let i = 1; i < gump.length; i++) {
          let dom = J('input[name=' + gump[i] + '_1]')
          if (dom.val()) {
            if (type == 1) {
              sum += parseFloat(dom.val())
            } else if (type == 2) {
              if (i < 2) {
                sum += parseFloat(dom.val())
              } else {
                sum -= parseFloat(dom.val())
              }
            } else if (type == 3) {
              sum *= parseFloat(dom.val())
            } else if (type == 4) {
              sum -= parseFloat(dom.val())
            }
          }
        }
        J('input[name=' + q + '_1]').val(sum.toFixed(2))
      },
      formrevise() {
        var name1 = ''
        var name2 = ''
        var name3 = ''
        if (this.$store.state.tableData[2]) {
          //不征税收入和税基类减免应纳税所得额明细表
          //41行
          let val41_1 = 1 * (this.$store.state.tableData[2][35].value)
          this.form.a6_1 = val41_1.toFixed(2)
        } else {
          name1 = '《不征税收入和税基类减免应纳税所得额明细表》'
        }
        if (this.$store.state.tableData[3]) {
          //固定资产加速折旧(扣除)明细表
          //5行6列
          let val5_6 = 1 * (this.$store.state.tableData[3][29].value)
          this.form.a7_1 = val5_6.toFixed(2)
        } else {
          name2 = '《固定资产加速折旧(扣除)明细表》'
        }
        if (this.$store.state.tableData[5]) {
          //减免所得税额明细表
          //30行
          let val30_1 = 1 * (this.$store.state.tableData[5][29].value)
          this.form.a12_1 = val30_1.toFixed(2)
        } else {
          name3 = '《减免所得税额明细表》'
        }
        if (name1 != '' || name2 != '' || name3 != '') {
          this.eg('请按照填表顺序正确填写:' + name1 + name2 + name3 + '表未填写！')
        }
      }
    }
  }
</script>

<style scoped>
  .p2 {
    text-align: center;
  }

  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    width: 900px;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
    margin: 0px auto;
  }

  input {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    padding: 3px 2px;
    text-align: left;
  }

  .bg-write {
    background: #ccccff;
  }

  .bg-unwrite {
    background: #d6ebff;
  }

  .bg-gray {
    background: #ddd;
  }
</style>



